@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body, html {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 100px);
  padding: 0 50px;
  display: flex;
}

.header__link-for-img {
  width: 40px;
  height: 40px;
}

.header__navlist {
  flex-direction: row;
  height: 100%;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__text {
  color: #060b35;
  text-transform: uppercase;
  margin: auto auto auto 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: transform .3s;
}

.header__text:last-of-type {
  margin-right: 0 !important;
}

.header__text:first-of-type {
  margin-left: 0 !important;
}

.header__text:hover {
  color: #00acdc;
  cursor: pointer;
}

.is-active {
  position: relative;
  color: #00acdc !important;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding-block: 50px;
  padding-inline: 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  transition: transform .3s;
  display: flex;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 32px 0 40px;
}

.product-card__name {
  color: #060b35;
  width: 166px;
  margin: 0 0 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.product-card:hover .product-card__name {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  text-align: left;
  width: 166px;
  margin: 0 0 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rating {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin: 0 0 24px;
  display: flex;
}

.product-card__reviews {
  text-align: right;
  height: 16px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 16px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin: 0 0 16px;
  display: flex;
}

.product-card__pricetext {
  text-align: left;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 18px;
}

.product-card__pricenumbers {
  text-align: right;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card .product-card__button {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 0 0 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  transition: background-color .3s;
}

.product-card .product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  transition: background-color .3s;
}

.product-card .stars {
  justify-content: space-around;
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.product-card .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-size: cover;
  width: 13px;
  height: 12px;
  margin-right: 4px;
  display: flex;
}

.product-card .stars__star:last-child {
  margin-right: 0;
}

.product-card .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
